:root {
  --blue-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav_item {
  height: 60px;
  margin-right: 20px;
  padding: 0;
  line-height: 60px;
}

.nav_list {
  text-align: center;
  text-transform: uppercase;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.logo-container {
  width: 40px;
  height: 40px;
}

.logo {
  width: 100%;
  height: auto;
  display: block;
}

.nav_link {
  color: #000;
  height: 60px;
  text-decoration: none;
}

.nav_link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  height: 4px;
  display: block;
  bottom: 0;
}
